
.selectBar{
    height: 45px;
    width: 100%;
    background-color: white;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 4;
    > div{
        background-color: white;
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        > div{
            display: flex;
            align-items: center;
            > span:first-child{
                margin-right: 5px;
            }
        }
    }
    > div:first-child{
        border-bottom: 1px solid #e8e8e8;
    }
    > .selectBox{
        width: 100vw;
        position: absolute;
        top: 100%;
        height: 290px;
        z-index: 2;
        background-color: white;
        display: flex;
        flex-direction: column;
        > div:first-child{
            width: 100%;
            flex: 1;
            display: flex;
            > div{
                height: 100%;
                flex: 1;
                
            }
        }
        > div:last-child{
            height: 50px;
            width: 100%;
            display: flex;
            align-items: center;
            border-top: 1px solid #e8e8e8;
            button{
                height: 100%;
                border: none;
                font-size: 18px;
            }
            button:first-child{
                width: 150px;
                background-color: #ffffff;
                color: #21b97a;
            }
            button:last-child{
                background-color: #21b97a;
                color: #ffffff;
                flex: 1;
            }
        }
    }
    > .shadeBox{
        width: 100vw;
        height: 100vh;
        background-color: black;
        opacity: 0.2;
        position: fixed;
        z-index: 1;
        top: 0;
        left: 0;
        right: 0;
    }
}

.modal{
    transform: 0 !important;
    top: 50px !important;
    max-width: 100vw !important;
    padding: 0 !important;
    margin: 0 !important;
    width: 100% !important;
    transition: 0s all !important;
    > *{
        max-width: 100vw !important;
        padding: 0 !important;
        margin: 0 !important;
        width: 100vw !important;
    }
}
.scrollableDiv{
    > div{
        > div{
            > div{
                width: calc(100% );
                padding: 20px 10px;
                position: relative;
                display: flex;
                align-items: center;
                justify-content: center;
                background-color: white;
                > div:first-child{
                    width: 120px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    > img{
                        width: 100% !important;
                    }
                }
                > div:last-child{
                    flex: 1;
                    margin-left: 10px;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    word-break: break-all;
                    white-space: nowrap;
                    > p{
                        text-overflow: ellipsis;
                        overflow: hidden;
                        width: 96%;
                    }
                    > p:first-child{
                        font-size: 18px;
                        font-weight: bold;
                    }
                    > p:not(:first-child){
                        font-size: 14px;
                        margin-top: 6px;
                    }
                    > div{
                        display: flex;
                        align-items: center;
                        margin-top: 6px;
                        div{
                            text-overflow: ellipsis;
                            overflow: hidden;
                            word-break: break-all;
                            white-space: nowrap;
                            margin-right: 10px;
                            padding: 2px 4px;
                            border-radius: 4px !important;
                            font-size: 14px;
                            background-color: #e1f5ed;
                            color: #39becd;
                        }
                    }
                    > span{
                        font-size: 18px;
                        color: orangered;
                        font-weight: bold;
                        span{
                            font-size: 14px;
                        }
                    }
                }
            }
            > div:not(:last-child)::after{
                position: absolute;
                bottom: 0;
                content: '';
                display: block;
                width: calc(100% - 10px * 2);
                height: 1px;
                background-color: black;
            }
        }
        
    }
}

.seleTor{
    padding-left: 10px;
    margin-bottom: 20px;
    > div:first-child{
        font-size: 14px;
        margin-left: 10px;
        height: 26px;
        padding-left: 10px;
        margin-bottom: 10px;
        display: flex;
        align-items: flex-end;
    }
    > div:last-child{
        > div{
            display: flex;
            flex-wrap: wrap;
            > div{
                margin-right: 14px;
            }
        }
    }
}
